@import url(./../../../variables.module.css);

.paginator {
    box-sizing: border-box;
    width: 100%;
    max-width: 600px;
    padding: 0px 10px;
    display: flex;
    justify-content: space-between;
}

@media (max-Width:780px) {
    .paginator button{
        font-size: 10px;
    }
}

.disabled, .disabled:hover {
    background-color: grey;
    color: white;
    border-color: transparent;
}