.userSearch {
    padding: 5px 10px;
    border: 2px solid black;
    background-color: white;
    border-radius: 8px;
}

.userSearch>h2 {
    text-decoration: underline;
    text-align: center;
    margin: 0px;
}

.pages {
    display: flex;
    justify-content: center;
}

.page,.disabled, .disabled:hover {
    background-color: white;
    color: black;
    border: 1px solid black;
    height: 40px;
    width: 25px;
    border-radius: 50% / 15px;
    transition: all 0s ease;
    font-size: 20px;
}